.container-settings {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  top: 5%;
  right: 10%;

  &:hover .updates {
    height: 400px;
  }
  .setting {
    font-size: 35px;
    cursor: pointer;
  }
  .updates {
    display: flex;
    // flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    height: 0;
    // width: 150px;
    transition: height 0.5s cubic-bezier(0.075, 0.82, 0.165, 1.2);
    background-color: $secondary-clear2-color;
    border-radius: 5px 0 5px 5px;

    .title-update {
      margin: 5px 10px;
      .title {
        font-weight: 700;
        margin-bottom: 5px;
        color: $primary-dark-color;
      }
      .update {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .num {
          padding: 5px;
        }
      }
    }

    button.btn-setting {
      padding: 6px;
      min-width: 32px;
      font-size: 20px;
      border-radius: 10px;
      cursor: pointer;
      border: none;
      color: $primary-dark-color;
    }

    .ex .title-update .inputs {
      display: flex;
      flex-direction: column;
      input {
        height: 25px;
        margin: 5px 6px;
        padding: 5px;
        outline: none;
        border: none;
        border-radius: 5px;
      }
    }
  }
}
