@import "./_variables";
@import "./Components/Settings";
@import "./Components/Counter";

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}

body {
  font-family: "Comfortaa", cursive;
  background-color: $primary-dark-color;
  color: $secondary-clear1-color;

  .App {
    position: relative;
    h1 {
      text-align: center;
      margin: 50px;
    }
  }

  em {
    color: $primary-clear-color;
    font-style: initial;
  }
}

button {
  transition: background 0.2s;
}
button:active {
  box-shadow: inset 1px 1px 5px;
}
button:hover {
  background-color: hsl(186, 28%, 87%);
}
