.container-counter {
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 30vw;
  height: 400px;

  .exercice,
  .timers {
    font-weight: 700;
    font-size: 50px;
    height: 40px;
  }

  .timers {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 300px;
    height: 100px;

    .timer {
      position: absolute;
      top: 0;
      transform: scale(0.4);
      opacity: 0.3;
    }
    .primary {
      position: absolute;
      bottom: 0;
      transition: transform 0.2s;
      white-space: nowrap;
    }
  }

  .laps {
    display: flex;
    justify-content: space-between;
    width: 100%;
    white-space: nowrap;

    .info {
      width: 43%;
    }

    .current-lap {
      text-align: end;
    }

    .message {
      text-align: center;
      font-weight: 700;
      font-size: 20px;
    }
  }

  .progress-bar {
    position: relative;
    width: 100%;
    height: 20px;
    border: 1px solid;
    border-radius: 10px;
    overflow: hidden;

    .current-progress {
      transition: width 0.2s;
      position: absolute;
      background: linear-gradient(
        220deg,
        $primary-clear-color,
        $secondary-clear2-color
      );
      width: 100%;
      height: 100%;
    }
  }

  .buttons {
    white-space: nowrap;

    button {
      font-size: 25px;
      padding: 15px;
      width: 55px;
      border-radius: 30%;
      border: none;
      margin: 20px 30px;
      cursor: pointer;
      color: $primary-dark-color;
    }
  }
}

//////////////////////// responsive

@media screen and (max-width: 1200px) {
  .container-counter {
    width: 50vw;
  }
}

@media screen and (max-width: 700px) {
  .container-counter {
    width: 75vw;
    height: 300px;

    .exercice,
    .timers {
      font-size: 2rem;
    }

    .laps {
      font-size: 12px;
    }
  }
}
